          <div v-if="item?.living" class="living">
            <div class="fl">
              <span class="dot"></span>
              <p>直播中</p>
            </div>
            <div class="fr">
              <p>
                {{
                  String(item.visitorCount).length > 4
                    ? `${Math.round(item.visitorCount / 1000) / 10}W`
                    : item.visitorCount
                }}
              </p>
            </div>
          </div>
          <p>{{ item.intro }}</p>
          <div class="shopCarts">
            <ul class="goods">
              <li
                v-for="g in item.shopCarts.goods"
                :key="g.itemId"
