  created() {
    this.$store.dispatch("getLiveTabheader");
    this.$store.dispatch("getLiveRoom", this.tabid);
  },

  updated() {
    this.$store.dispatch("getLiveRoom", this.tabid);
  },
  computed: {
    ...mapState(["livetabheader", "liveroom"]),
